<template>
  <div class="sheep-game">
    <div class="game-header">
      <h1>Áæä‰∫Ü‰∏™Áæä</h1>
      <div class="game-info">
        <div class="score">ÂæóÂàÜ: {{ score }}</div>
        <button @click="resetGame" class="reset-btn">ÈáçÊñ∞ÂºÄÂßã</button>
      </div>
    </div>

    <div class="game-container">
      <!-- Ê∏∏Êàè‰∏ªÂå∫Âüü -->
      <div class="game-board">
        <div 
          v-for="(layer, zIndex) in board" 
          :key="zIndex" 
          class="board-layer"
          :style="{ zIndex: zIndex }"
        >
          <div
            v-for="(row, y) in layer"
            :key="y"
            class="board-row"
          >
            <div
              v-for="(cell, x) in row"
              :key="x"
              class="board-cell"
              :class="{ 
                'has-block': cell !== null, 
                'top-block': isTopBlock(x, y, zIndex),
                'selected': selectedBlocks.some(b => b.x === x && b.y === y && b.z === zIndex)
              }"
              @click="selectBlock(x, y, zIndex)"
            >
              <div v-if="cell !== null" class="block" :class="'block-' + cell">
                {{ getEmojiForType(cell) }}
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Â∫ïÈÉ®ÊßΩ‰ΩçÂå∫Âüü -->
      <div class="slot-area">
        <h3>ÊßΩ‰Ωç</h3>
        <div class="slots">
          <div 
            v-for="(slot, index) in slots" 
            :key="index" 
            class="slot"
            :class="{ 'filled': slot !== null }"
          >
            <div v-if="slot !== null" class="block" :class="'block-' + slot">
              {{ getEmojiForType(slot) }}
            </div>
          </div>
        </div>
        <div class="slot-info">
          <p>Ââ©‰ΩôÁ©∫Èó¥: {{ 7 - filledSlots }} / 7</p>
        </div>
      </div>
    </div>

    <!-- Ê∏∏ÊàèÁä∂ÊÄÅÊèêÁ§∫ -->
    <div v-if="gameOver" class="game-over-overlay">
      <div class="game-over-message">
        <h2>{{ gameWin ? 'ÊÅ≠ÂñúÈÄöÂÖ≥!' : 'Ê∏∏ÊàèÁªìÊùü' }}</h2>
        <p>{{ gameWin ? '‰Ω†ÊàêÂäüÊ∂àÈô§‰∫ÜÊâÄÊúâÊñπÂùóÔºÅ' : 'ÊßΩ‰ΩçÂ∑≤Êª°ÔºåÊ∏∏ÊàèÁªìÊùü„ÄÇ' }}</p>
        <button @click="resetGame">ÂÜçÁé©‰∏ÄÊ¨°</button>
      </div>
    </div>

    <!-- ÁßªÂä®Á´ØÊìç‰ΩúËØ¥Êòé -->
    <div class="instructions" v-if="isMobile">
      <p>ÁÇπÂáªÊúÄ‰∏äÂ±ÇÁöÑÊñπÂùóÂ∞ÜÂÖ∂ÁßªËá≥Â∫ïÈÉ®ÊßΩ‰ΩçÔºå‰∏â‰∏™Áõ∏ÂêåÂõæÊ°àÂèØÊ∂àÈô§</p>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'

// ÂÆö‰πâÊñπÂùóÁ±ªÂûã
const BLOCK_TYPES = [
  'üçì', 'üçä', 'üçá', 'üçâ', 'üçå', 'üçé', 'üçë',  // Ê∞¥ÊûúÁ±ª
  'üê∂', 'üê±', 'üê≠', 'üêπ', 'üê∞', 'ü¶ä', 'üêª',  // Âä®Áâ©Á±ª
  'üöó', 'üöï', 'üöô', 'üöå', 'üöé', 'üèé', 'üöì'   // ‰∫§ÈÄöÂ∑•ÂÖ∑Á±ª
]

type BoardType = Array<Array<Array<number | null>>>
type SlotsType = Array<number | null>

// Ê∏∏ÊàèÁä∂ÊÄÅ
const board = ref<BoardType>([])
const slots = ref<SlotsType>(Array(7).fill(null))
const selectedBlocks = ref<Array<{x: number, y: number, z: number}>>([])
const score = ref(0)
const gameOver = ref(false)
const gameWin = ref(false)

// Ê£ÄÊµãÊòØÂê¶‰∏∫ÁßªÂä®Á´Ø
const isMobile = computed(() => {
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
})

// ËÆ°ÁÆóÂ∑≤Â°´ÂÖÖÁöÑÊßΩ‰ΩçÊï∞Èáè
const filledSlots = computed(() => {
  return slots.value.filter(slot => slot !== null).length
})

// ÂàùÂßãÂåñÊ∏∏Êàè
const initGame = () => {
  // ÂàõÂª∫‰∏Ä‰∏™3Â±ÇÁöÑÁÆÄÂçïÂÖ≥Âç°
  const newBoard: BoardType = []
  
  // Á¨¨‰∏ÄÂ±Ç - Â∫ïÂ±Ç (3x3)
  const layer1: Array<Array<number | null>> = []
  for (let y = 0; y < 3; y++) {
    const row: Array<number | null> = []
    for (let x = 0; x < 3; x++) {
      row.push(Math.floor(Math.random() * BLOCK_TYPES.length))
    }
    layer1.push(row)
  }
  newBoard.push(layer1)
  
  // Á¨¨‰∫åÂ±Ç (3x3)ÔºåÈÉ®ÂàÜË¶ÜÁõñÁ¨¨‰∏ÄÂ±Ç
  const layer2: Array<Array<number | null>> = []
  for (let y = 0; y < 3; y++) {
    const row: Array<number | null> = []
    for (let x = 0; x < 3; x++) {
      // ÈÉ®ÂàÜÊñπÂùóË¶ÜÁõñ‰∏ãÂ±Ç
      if (Math.random() > 0.3) {
        row.push(Math.floor(Math.random() * BLOCK_TYPES.length))
      } else {
        row.push(null)
      }
    }
    layer2.push(row)
  }
  newBoard.push(layer2)
  
  // Á¨¨‰∏âÂ±Ç - È°∂Â±Ç (2x2)ÔºåÊîæÂú®‰∏≠Èó¥
  const layer3: Array<Array<number | null>> = []
  for (let y = 0; y < 5; y++) {
    const row: Array<number | null> = []
    for (let x = 0; x < 5; x++) {
      // Âú®‰∏≠Èó¥ÊîæÁΩÆ2x2ÁöÑÊñπÂùó
      if (y >= 1 && y <= 2 && x >= 1 && x <= 2) {
        row.push(Math.floor(Math.random() * BLOCK_TYPES.length))
      } else {
        row.push(null)
      }
    }
    layer3.push(row)
  }
  newBoard.push(layer3)
  
  board.value = newBoard
  slots.value = Array(7).fill(null)
  selectedBlocks.value = []
  score.value = 0
  gameOver.value = false
  gameWin.value = false
}

// Âà§Êñ≠ÊòØÂê¶‰∏∫ÊúÄ‰∏äÂ±ÇÊñπÂùó
const isTopBlock = (x: number, y: number, z: number): boolean => {
  // Â¶ÇÊûúÂ∑≤ÁªèÊòØÊúÄÈ´òÂ±ÇÔºåÂàôÊÄªÊòØÊúÄ‰∏äÂ±ÇÊñπÂùó
  if (z === board.value.length - 1) {
    return board.value[z]?.[y]?.[x] !== null
  }
  
  // Ê£ÄÊü•‰∏äÊñπÂ±ÇÊòØÂê¶ÊúâÊñπÂùóË¶ÜÁõñ
  for (let i = z + 1; i < board.value.length; i++) {
    // ‰ΩøÁî®ÂèØÈÄâÈìæÊìç‰ΩúÁ¨¶ÂÆâÂÖ®ËÆøÈóÆ
    if (board.value[i]?.[y]?.[x] !== undefined && 
        board.value[i]?.[y]?.[x] !== null) {
      return false
    }
  }
  
  return board.value[z]?.[y]?.[x] !== null
}

// ÈÄâÊã©ÊñπÂùó
const selectBlock = (x: number, y: number, z: number) => {
  // Â¶ÇÊûúÊ∏∏ÊàèÂ∑≤ÁªìÊùüÔºå‰∏çËÉΩÊìç‰Ωú
  if (gameOver.value) return
  
  // Âè™ËÉΩÈÄâÊã©ÊúÄ‰∏äÂ±ÇÁöÑÊñπÂùó
  if (!isTopBlock(x, y, z)) return
  
  // Ê£ÄÊü•Á¥¢ÂºïÊòØÂê¶ÊúâÊïà
  if (!board.value[z] || !board.value[z][y]) return
  
  const blockType = board.value[z][y][x]
  if (blockType === null) return
  
  // Ê£ÄÊü•ÊßΩ‰ΩçÊòØÂê¶Â∑≤Êª°
  if (filledSlots.value >= 7) {
    // ÊßΩ‰ΩçÊª°‰∫ÜÔºåÊ£ÄÊü•ÊòØÂê¶ËÉΩÊ∂àÈô§
    checkElimination()
    if (filledSlots.value >= 7) {
      // ‰ªçÁÑ∂Êª°‰∫ÜÔºåÊ∏∏ÊàèÁªìÊùü
      gameOver.value = true
      gameWin.value = false
      return
    }
  }
  
  // Â∞ÜÊñπÂùóÊ∑ªÂä†Âà∞ÊßΩ‰Ωç
  const emptySlotIndex = slots.value.findIndex(slot => slot === null)
  if (emptySlotIndex !== -1 && blockType !== null && blockType !== undefined) {
    slots.value[emptySlotIndex] = blockType
    
    // Ê†áËÆ∞‰∏∫Â∑≤ÈÄâÊã©ÔºàËßÜËßâÊïàÊûúÔºâ
    selectedBlocks.value.push({x, y, z})
    
    // ‰ªéÊ∏∏ÊàèÊùø‰∏äÁßªÈô§ÊñπÂùó
    if (board.value[z] && board.value[z][y]) {
      board.value[z][y][x] = null
    }
    
    // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Ê∂àÈô§
    checkElimination()
    
    // Ê£ÄÊü•ÊòØÂê¶Ëé∑ËÉú
    checkWinCondition()
  }
}

// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Ê∂àÈô§
const checkElimination = () => {
  // ÁªüËÆ°ÊØè‰∏™Á±ªÂûãÁöÑÊñπÂùóÊï∞Èáè
  const countMap: { [key: number]: number } = {}
  
  slots.value.forEach(slot => {
    if (slot !== null) {
      if (countMap[slot]) {
        countMap[slot]++
      } else {
        countMap[slot] = 1
      }
    }
  })
  
  // Êü•ÊâæÊï∞ÈáèÂ§ß‰∫éÁ≠â‰∫é3ÁöÑÁ±ªÂûã
  for (const [type, count] of Object.entries(countMap)) {
    const typeNum = parseInt(type)
    if (count >= 3) {
      // Ê∂àÈô§Ëøô‰∫õÊñπÂùó
      let removed = 0
      for (let i = 0; i < slots.value.length && removed < 3; i++) {
        if (slots.value[i] === typeNum) {
          slots.value[i] = null
          removed++
        }
      }
      
      // Â¢ûÂä†ÂàÜÊï∞
      score.value += 300
    }
  }
}

// Ê£ÄÊü•ËÉúÂà©Êù°‰ª∂
const checkWinCondition = () => {
  // Ê£ÄÊü•Ê∏∏ÊàèÊùøÊòØÂê¶‰∏∫Á©∫
  const isEmpty = board.value.every(layer => 
    layer.every(row => 
      row.every(cell => cell === null)
    )
  )
  
  if (isEmpty) {
    gameOver.value = true
    gameWin.value = true
  }
}

// Ëé∑ÂèñÊñπÂùóÁ±ªÂûãÁöÑemoji
const getEmojiForType = (type: number): string => {
  return BLOCK_TYPES[type] || 'üß©'
}

// ÈáçÁΩÆÊ∏∏Êàè
const resetGame = () => {
  initGame()
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊ∏∏Êàè
onMounted(() => {
  initGame()
})
</script>

